import TopBarSection from '@/components/TopBarSection';
import HightLightArticleSection from '@/components/HightLightArticleSection';
import ContactSection from '@/components/ContactSection';
import FooterSection from '@/components/FooterSection';
import CustomHeadComponent from '@/components/common/CustomHeadComponent';
import ContentSection from '@/components/portofolio/ContentSection';
import ProjectsSection from '@/components/home/ProjectsSection';
import MenuSection from '@/components/MenuSection';

import { MetaSeoModel } from '@/model/props';
import { useState } from 'react';
import useSWR from 'swr';

const fetcher = (url: string) => fetch(url).then((res) => res.json());

export default function PortofolioPage() {
    const [showModal, setShowModal] = useState(false);

    const { data, isLoading, error } = useSWR<MetaSeoModel>(`${process.env.BASE_URL + 'api/seo/portofolio'}`, fetcher);
    if (isLoading) return <></>
    if (error) return <></>
    if (!data) return <></>

    function dismissModal() {
        setShowModal(false);
    }

    function revealModal() {
        setShowModal(!showModal);
    }

    return (
        <div className="flex flex-col justify-start">
            <CustomHeadComponent props={data} />
            {showModal ? <MenuSection props={{ dismissModal }} /> : null}
            <div className="w-full flex flex-row justify-center bg-primary">
                <div className='lg:w-[1200px] md:w-[768px] sm:w-[608px] xs:w-[280px] flex flex-col justify-between'>
                    <TopBarSection props={{ backgroundColor: 'bg-primary', revealModal }} />
                </div>
            </div>
            <ContentSection />
            <ProjectsSection props={{
                titleClass: 'text-primary text-start leading-none',
                showButton: true,
                paddingTop: 'lg:pt-10 md:pt-10 sm:pt-10 xs:pt-10',
                paddingBot: 'lg:pb-20 md:pb-20 sm:pb-20 xs:pb-20'
            }} />
            <HightLightArticleSection />
            <ContactSection />
            <FooterSection />
        </div>
    )
}